.wrapper_location{
      width: 100%;
      height: 100%;
      position: relative;
      padding: 5rem 0;
}

.location_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}

.location_banner:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(var(--bg), transparent, var(--bg));
}

.container.location{
      justify-content: flex-end;
}

.location_column{
      padding-top: 20rem;
      width: 50%;
      flex-direction: column;
      align-items: center;
}

.location_column__date{
      flex-direction: column;
      align-items: center;
}

.location_column__date strong{
      text-transform: uppercase;
      font-size: 4rem;
      font-family: var(--ft-t-3);
      font-weight: 500;
}

.location_date__box{
      width: 32rem;
      height: 13rem;
      position: relative;
}

.location_date__box:before{
      width: .2rem;
      height: 100%;
      background: rgba(255,255,255,.4);
      top: 0;
      left: calc(50% - .1rem);
}

.date_box__column{
      flex-direction: column;
      align-items: center;
      justify-content: center;
}

.date_box__column h1{
      font-size: 12rem;
}

.date_box__column span{
      text-transform: uppercase;
      font-family: var(--ft-t-3);
      font-size: 4rem;
}

.date_box__column h3{
      font-size: 3.8rem;
}

.location_hours{
      margin-top: 2rem;
}

.location_hours h2{
      font-size: 4rem;
}

.location_icon{
      margin-top: 2rem;
}

.location_icon *{
      font-size: 5rem;
}

.location_column__addrees{
      flex-direction: column;
      align-items: center;
      margin-top: 2rem;
      font-family: var(--ft-t-2);
}

.location_column__addrees p{
      font-size: 2rem;
}

.location_column__addrees strong{
      font-size: 3rem;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
      .container.location{
            justify-content: center;
      }

      .location_column{
            width: 100%;
            margin-top: 5rem;
      }
}