.wrapper_gratitude{
      width: 100%;
      height: auto;
      padding: 12rem 0;
      font-family: var(--ft-s-2);
}

.container.gratitude{
      margin-top: 5rem;
      flex-direction: column;
      align-items: center;
}

.gratitude_message{
      margin-top: 20rem;
      width: 70%;
}

.gratitude_message p{
      font-size: 2.4rem;
}

.gratitude_separetor{
      width: 54rem;
      height: 6rem;
      position: relative;
      margin: 3rem 0;
}

.gratitude_separetor:before{
      width: 100%;
      height: .5rem;
      background: var(--text);
      left: 0;
      top: calc(50% - .25rem);
}

.gratitude_icon{
      position: relative;
      background: var(--bg);
      padding: 1rem;
}

.gratitude_icon *{
      font-size: 3.5rem;
}

.gratitude_warning p{
      font-size: 2.4rem;
}


/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
      .gratitude_message{
            margin-top: 30rem;
            width: 90%;
      }
}