.wrapper_countdown{
      width: 100%;
      height: auto;
      padding: 10rem 0;
}

.container.countdown{
      flex-direction: column;
      align-items: center;
}

.countdown_title{
      margin-top: 20rem;
      width: fit-content;
      height: fit-content;
      border-bottom: solid .5rem var(--text);
      border-top: solid .5rem var(--text);
}

.countdown_title h1{
      font-size: 10rem;
      padding: 0 2rem;
      font-family: var(--ft-t-3);
      text-transform: uppercase;
      letter-spacing: .2rem;
}

.countdown_time{
      width: 54rem;
      height: auto;
      margin-top: 4rem;
      gap: 2rem;
}

.countdown_time__item{
      position: relative;
      width: 12rem;
      height: 14rem;
      background: var(--text);
      border-radius: .6rem;
}

.countdown_time__item h3{
      color: var(--bg);
      font-size: 5rem;
}

.countdown_time__item span{
      width: 8rem;
      height: 4rem;
      position: absolute;
      left: 50%;
      bottom: 0;
      background: var(--bg);
      transform: translate(-50%, 50%);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-family: var(--ft-t-3);
      border-radius: .4rem;
      font-size: 2rem;
}

.countdown_message{
      margin-top: 5rem;
      width: 60%;
}

.countdown_message p{
      font-family: var(--ft-s-2);
      font-size: 2.4rem;
}

@media only screen and (max-width: 600px){
      .countdown_title{
            margin-top: 30rem;
      }

      .countdown_title h1{
            font-size: 8rem;
      }

      .countdown_message{
            width: 90%;
      }
}