@import url('https://fonts.googleapis.com/css2?family=Arizonia&family=Courgette&family=Domine:wght@400..700&family=Playwrite+AU+NSW:wght@100..400&family=Saira+Extra+Condensed:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root{
    --ft-s-1: 'Arizonia', cursive;
    --ft-s-2: 'Courgette', cursive;
    --ft-t-1: 'Domine', serif;
    --ft-t-2: 'Playwrite AU NSW', cusive;
    --ft-t-3: 'Saira Extra Condensed', sans-serif;

    --text: #fff;
    --bg: #242424;
    --alpha: rgba(36,36,36,0);

    font-size: 62.5%;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:before,
:after{
    content: '';
    position: absolute;
}

body {
    font-size: 1.6rem;
    font-family: var(--ft-t-1);
    overflow-x: hidden;
    color: var(--text);
    background: var(--bg);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

h1, h2, h3, h4{
    line-height: 110%;
}

img{
    width: 100%;
    object-fit: cover;
}

.flex{
    display: flex;
}

.center{
    align-items: center;
    justify-content: center;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 108rem;
    height: auto;
    margin: 0 auto;
    position: relative;
}

.section{
    height: 82rem;
    width: 100%;
    position: relative;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .section{
        height: auto;
    }
}

