.wrapper_couple{
      width: 100%;
      height: 100%;
      position: relative;
}

.couple_back,
.couple_back__banner{
      width: 100%;
      height: 100%;
}

.couple_back{
      position: absolute;
      top: 0;
      left: 0;
}

.couple_back__banner{
      position: relative;
      overflow: hidden;
}

.couple_back__banner:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(var(--alpha) 30%, var(--bg) 90%);
}

.couple_front{
      position: relative;
      width: 100%;
      height: 100%;
      padding-top: 8rem;
}

.container.couple{
      flex-direction: column;
      align-items: center;
}

.couple_images{
      width: 42.3rem;
      height: 52rem;
      position: relative;
}

.couple_image__front{
      width: 37.5rem;
      bottom: 0;
      left: calc(50% - 17.75rem);
      position: absolute;
}

.couple_title h1{
      font-family: var(--ft-s-1);
      font-size: 10rem;
      line-height: 8rem;
      font-weight: 500;
}

.couple_welcome{
      margin-top: 3rem;
      align-items: center;
      gap: 2rem;
}

.couple_welcome *{
      font-size: 4rem;
}

.couple_welcome p{
      font-size: 2.4rem;
      font-family: var(--ft-s-2);
}

.couple_message{
      width: 80%;
      margin-top: 3rem;
      font-family: var(--ft-s-2);
      font-size: 2.4rem;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
      .couple_title h1{
            font-size: 8rem;
      }

      .couple_welcome{
            gap: 1rem;
      }
}

