.couple_content.section{
      height: 99rem;
}

.img_border__top{
      width: 66.7rem;
      height: 37rem;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, -20rem);
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
      .couple_content.section{
            height: auto;
      }

      .img_border__top{
            width: 80%;
            height: auto;
            transform: translate(-50%, 0);
      }
}

